/* src/styles/index.css */

/* Reset default margin and padding */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Set a base font and color */
body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
  color: #333;
  background-color: #f4f4f4;
}

/* Add some padding and center content */
.container {
  width: 80%;
  margin: 0 auto;
  padding: 20px;
}

/* Style the Navbar */
.navbar {
  background: #000; /* Set the navbar background to black */
  color: #fff;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar a {
  color: #fff;
  text-decoration: none;
  margin: 0 10px;
}

.navbar a:hover {
  text-decoration: underline;
}

/* Style the header */
header {
  background: #333;
  color: #fff;
  padding: 20px 0;
  text-align: center;
}

header h1 {
  margin: 0;
  font-size: 2.5rem;
}

/* Style the footer */
footer {
  background: #333;
  color: #fff;
  padding: 10px 0;
  text-align: center;
  position: fixed;
  width: 100%;
  bottom: 0;
}

/* Add responsive design */
@media (max-width: 768px) {
  .navbar {
      flex-direction: column;
  }

  .navbar a {
      margin: 5px 0;
  }
}